module.exports = "/* Not a valid style option ratio, but used as the mobile base for 8x1 and 20x1 */\n.message__content {\n    display: flex;\n    padding-right: 1rem;\n}\n\n.message__logo-container {\n    flex: 0 0 33%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n.message__logo {\n    width: 60%;\n}\n.message__messaging {\n    flex: 1 1 100%;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: flex-start;\n}\n.message__messaging > * {\n    width: 100%;\n}\n.message__headline {\n    font-size: 5vw;\n    line-height: 1;\n    margin-bottom: 0.2em;\n}\n.message__headline span.multi:nth-of-type(2) {\n    display: none;\n}\n.message__disclaimer {\n    font-size: 10px;\n    line-height: 1.1;\n}\n@media (max-aspect-ratio: 61/10) and (min-width: 400px) {\n    .message__headline {\n        font-size: 1.5rem;\n        margin-bottom: 0.5rem;\n    }\n\n    .message__disclaimer {\n        font-size: 0.75rem;\n        line-height: 1;\n        padding-right: 8%;\n    }\n}\n@media (max-aspect-ratio: 61/10) and (min-width: 520px) {\n    .message__logo-container {\n        flex: 0 0 25%;\n    }\n    .message__headline {\n        margin: 0 0 2% 0;\n        font-size: 3.7vw;\n        padding-right: 2%;\n        line-height: 1.1;\n    }\n    .message__headline span.multi:nth-of-type(2) {\n        display: inline;\n    }\n\n    .message__headline span.multi:nth-of-type(1) {\n        display: none;\n    }\n    .message__disclaimer {\n        font-size: 0.9rem;\n    }\n    @media (max-aspect-ratio: 61/10) and (min-width: 640px) {\n        .message__headline {\n            font-size: 1.7rem;\n        }\n    }\n}\n"